var win = window;
var doc = document;

// Reacttouch
React.initializeTouchEvents(true);

// 
require("./touchEffect").attach(doc.body);

// 
var env = require('./env');

var classnames = require('classnames');

/**
 * Top namespace
 */
var Context = {
    getTID,
    mixin: redo(mixin),
    noop(v) {
        return v;
    },
    rem,
    makePrivateRem
};

Context.mixin(Context, env, env.event);

// console.dir(Context);

/**
 * 
 * @param  {Object} receiver
 * @param  {Object} supplier
 * @return {Object} receiver
 */
function mixin(receiver, supplier) {
    if (Object.keys) {
        Object.keys(supplier).forEach(function (property) {
            Object.defineProperty(receiver, property, Object.getOwnPropertyDescriptor(supplier, property));
        });
    } else {
        for (var property in supplier) {
            if (supplier.hasOwnProperty(property)) {
                receiver[property] = supplier[property];
            }
        }
    }
    return receiver;
}


/**
 * 
 * @param  {Function} fn 
 * @return {Function} 
 * @demo
 *      function add(x, y) { return x+y; }
 *      add = redo(add);
 *      add(1,2,3) => 6
 */
function redo(fn) {
    return function () {
        var args = arguments;
        var ret = fn(args[0], args[1]);
        for (var i = 2, l = args.length; i < l; i++) {
            ret = fn(ret, args[i]);
        }
        return ret;
    }
}


/**
 * id
 * @return {Number}
 */
var tid = 0;
function getTID() {
    return tid++;
}


/**
 * rem system
 * @TOTO  + rem + makePrivateRem
 */
(function(docEl, fontEl) {
    var dpr = win.devicePixelRatio || 1;

    // 2webview webkit534
    var matches = navigator.userAgent.match(/Android[\S\s]+AppleWebkit\/?(\d{3})/i);
    if (matches && matches[1] <= 534) {
        dpr = 1;
    }

    win.dpr = dpr;
    docEl.setAttribute('data-dpr', dpr);
    docEl.firstElementChild.appendChild(fontEl);

    win.addEventListener('resize', function() {
        // resizechange,pad
        setRem();
    }, false);
    win.addEventListener('pageshow', function(e) {
        if (e.persisted) {
            setRem();
        }
    }, false);

    setRem();

    function setRem() {
        var docWidth = docEl.clientWidth;
        win.rem = docWidth / 10;

        // ZTE  ZTE U930_TD/1.0 Linux/2.6.39/Android/4.0Release/3.5.2012 Browser/AppleWebkit534.30
        // bug rem=html fontsize
        if (/ZTE U930_TD/.test(navigator.userAgent)) {
            win.rem = win.rem * 1.13;
        }

        fontEl.innerHTML = 'html{font-size:' + win.rem + 'px!important}';
    }
})(doc.documentElement, doc.createElement('style'));

var defaultArtBoardWidth = 750;

function rem(px, artBoardWidth) {
    artBoardWidth = artBoardWidth || defaultArtBoardWidth;
    return (px * 10 / artBoardWidth) + 'rem';
}

function makePrivateRem(artBoardWidth) {
    return function (px) {
        return rem(px, artBoardWidth);
    }
}


/**
 * bodyclassName
 */
doc.documentElement.className = classnames(doc.documentElement.className.trim(), {
    pc:       env.is.pc,
    mobile:   env.is.mobile,
    hairline: env.support.hairline
});

module.exports = Context;

/**
 * TODO: modernizr env
 */



